<template>
  <div class="wiki-publish-article">
    <publicHeader></publicHeader>
    <div class="wiki-publish-article-title">
      <el-input type="text" placeholder="请输入标题" v-model="text" maxlength="15" show-word-limit></el-input>
    </div>
    <div class="wiki-publish-article-block">
      <div>
        <span class="demonstration">请选择发表栏目:</span>
        <el-cascader expand-trigger="hover" :options="options" v-model="selectedOptions"></el-cascader>

        <span class="demonstration">请选择技术点:</span>
        <el-cascader expand-trigger="hover" :options="options1" v-model="selectPoints"></el-cascader>

        <span class="demonstration">请选择可查看用户等级:</span>
        <el-cascader expand-trigger="hover" :options="options2" v-model="selectLevel"></el-cascader>
      </div>
    </div>
    <div class="wiki-publish-article-md">
      <div>
        <md
        v-model="content"
        ></md>
        <el-button type="primary" @click="submission">发表</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import publicHeader from "@/components/publicHeader";
import md from "@/components/MarkdownEditor";
import * as ajax from "@/api";

export default {
  components: { publicHeader, md },
  data() {
    return {
      content: "",
      selectedOptions: "", //选择类型
      selectPoints: "", //技术点
      selectLevel:'', //等级
      options: [],
      options1: [],
      options2:[],
      text: ""
    };
  },
  created() {},
  methods: {
    submission() {
      if (confirm("确定提交吗？")) {
      } else {
      }
    },

    submission() {
      if (!this.text) {
        alert("标题不能为空");
      } else if (!this.selectedOptions) {
        alert("请选择发表栏目");
      }else if(!this.selectPoints){
        alert('请选择发表的技术点')
      } else if(!this.selectLevel){
        alert('请选择用户查看等级')
      }else if (!this.content) {
        alert("文章不能为空");
      } else {
        // ajax.publishArticle(this.selectedOptions[1], this.text,this.selectLevel[0] ,this.selectPoints[0],this.content)
        //   .then(resp => {
        //     if (resp.data.code) {
        //       alert('发表成功')
        //       this.$router.push("/mine");
        //     } else {
        //       this.$message.error(resp.msg);
        //     }
        //   });
        console.log(this.selectedOptions[1], this.text,this.selectLevel[0] ,this.selectPoints[0],this.content)
      }
    }
  },
  created() {
    ajax.getType().then(resp => {
      if (resp.data.code) {
        let list = resp.data.data.all_info;
        list.forEach(element => {
          element["value"] = element["type"]["id"];
          element["label"] = element["type"]["type_name"];
          element["children"] = element["books"];
          element["children"].forEach(item => {
            item["value"] = item["id"];
            item["label"] = item["book_name"];
          });
        });
        this.options = list; //类型列表
        let list1 = resp.data.data.all_content_types; 
        list1.forEach(item => {
          item["value"] = item["id"];
          item["label"] = item["type"];
        });
        this.options1 = list1; //技术点列表
      }
    });

    ajax.getLookLevel().then(resp => {
      if(resp.data.code){
        let list = resp.data.data.limit_level
        list.forEach(item=>{
          item["value"] = item["level"];
          item["label"] = item["level_describe"];
        })
        this.options2 = list //查看等级列表
      }
    });
  }
};
</script>

<style lang='scss'>
.wiki-publish-article {
  min-height: 100%;
  &-text {
    width: 100%;
    height: auto;
    padding: 30px;
  }
  &-title {
    width: 100%;
    .el-input {
      width: 1280px;
      margin: 0 auto;
      margin-top: 30px;
      display: block;
    }
  }
  &-block {
    margin: 20px 0 20px 0;
    > div {
      width: 1280px;
      margin: 0 auto;
      .demonstration {
        margin-right: 20px;
      }
      .el-input--suffix {
        width: 200px;
        margin-right: 20px;
      }
    }
  }

  .wiki-publish-article-md {
    width: 100%;
    margin-bottom: 50px;
    >div{
      width: 1280px;
      margin: 0 auto;
      position: relative;
    }
    .auto-height{
      margin: 0 auto;
      width: 1280px;
      min-height: 500px;
      .tui-editor-defaultUI{
        min-height: 500px;
        .te-editor-section{
          min-height: 500px;
          .tui-editor{
            min-height: 500px;
            .tui-editor-contents{
              word-wrap:break-word;
            }
          }
        }
        .CodeMirror-sizer{
          padding-bottom:78px !important;
        }
      }
    }
  }

  .el-button {
    width: 100px;
    height: 30px;
    line-height: 9px;
    font-size: 12px;
    text-align: center;
  }
  .el-button--primary {
    position: absolute;
    bottom: -52px;
    right: 0;
  }
}
</style>
